import React from 'react';
import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';
import dentist1Img from '../photos/dentist1Img.jpg'

const Home = () => {
  return (
    <div>
      <header>
        <div className="contact-info">
          <h2>Contact Us</h2>
          <p>
            Address:{' '}
            <a
              href="https://www.google.com/maps?q=12685+Oak+Hill+Way"
              target="_blank"
              rel="noopener noreferrer"
            >
              12685 Oak Hill Way, Florida
            </a>
          </p>
          <p>Phone: <a href="tel:+19415252555">+1 941-525-2555</a></p>
        </div>
      </header>
      <div className="bio-box">
      <h1>Welcome to the Happy Dental Office!</h1>
      <p>
        We are a premier dental office dedicated to providing exceptional
        dental care in Florida. Our experienced team of dentists and staff
        offers a wide range of dental services to meet the needs of our
        patients. Whether you require routine dental check-ups, advanced
        treatments, or cosmetic procedures, we are here to help you achieve
        optimal oral health and a beautiful smile.
      </p>
      <p>
        At our dental office, we prioritize patient comfort and satisfaction.
        We utilize state-of-the-art technology and adhere to strict sterilization
        protocols to ensure a safe and comfortable environment. Our friendly and
        knowledgeable team is committed to delivering personalized care and
        building long-lasting relationships with our patients.
      </p>
      <p>
        Contact us today to schedule an appointment and experience the
        exceptional dental services we provide. We look forward to serving you
        and your family for all your dental needs.
      </p>
      </div>
      <Tabs>
        <TabList>
          <Tab>Dentist 1</Tab>
          <Tab>Dentist 2</Tab>
          <Tab>Dentist 3</Tab>
        </TabList>

        <TabPanel>
        <div className="dentist-profile">
    <img src={dentist1Img} alt="Dentist 1" />
    <div>
      <h3>Dentist 1</h3>
      <p>Dr. Leo Tran</p>
    </div>
    <ul>
      <li>Education: DDS, University of XYZ</li>
      <li>Experience: 2 years</li>
      <li>Specialties: Cosmetic Dentistry, Orthodontics</li>
    </ul>
  </div>
        </TabPanel>
        <TabPanel>
          <h3>Dentist 2</h3>
          <p>Dr. Brian</p>
          <ul>
            <li>Education: DMD, University of ABC</li>
            <li>Experience: 2 years</li>
            <li>Specialties: Pediatric Dentistry, Dental Implants</li>
          </ul>
        </TabPanel>
        <TabPanel>
          <h3>Dentist 3</h3>
          <p>Dr. Andre</p>
          <ul>
            <li>Education: DMD, University of DEF</li>
            <li>Experience: 2 years</li>
            <li>Specialties: Endodontics, Oral Surgery</li>
          </ul>
        </TabPanel>
      </Tabs>
    </div>
  );
};

export default Home;